import {Component, DoCheck, OnInit} from '@angular/core';
import {Administrator} from '../entity';
import {AdministratorService} from '../administrator.service';

@Component({
  selector: 'app-administrator',
  templateUrl: './administrator.component.html',
  styleUrls: ['./administrator.component.css']
})
export class AdministratorComponent implements OnInit, DoCheck {
  private administrators: Administrator[];
  private dataShow: Administrator[]; // 
  public leng: number;                 // 
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  private no: string;

  constructor(private administratorService: AdministratorService) {
  }

  ngOnInit() {
    this.getAdministrators();
  }

// DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for (let i = 0; i < this.pagNums; i++, startNum++) {
        if (this.administrators[startNum] !== null && this.administrators[startNum] !== undefined) {
          this.dataShow.push(this.administrators[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }

// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }

// 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }

// Administrator
  getAdministrators(): void {
    this.administratorService.getAdministrators()
      .subscribe(res => {
        this.administrators = res;
        this.leng = res.length;
      });
  }

  getAdministratorsByNo(des: string): void {
    if (!des) {
      alert('!');
    } else {
      this.no = des;
      this.administratorService.getAdministratorsByNo(this.no.trim())
        .subscribe(res => {
          this.administrators = res;
          if (this.administrators[0] !== undefined) {
            this.dataShow = [];
            this.dataShow.push(this.administrators[0]);
            this.currentPag = 1;
          } else {
            alert('!');
          }
          this.leng = res.length;
        });
    }
  }

  fresh(): void {
    window.location.reload();
  }

  delete(administrator: Administrator | number): void {
    // tslint:disable-next-line:variable-name
    const confirm_ = confirm('?');
    if (confirm_) {
      this.administratorService.deleteAdministrator(administrator)
        .subscribe(res => {
          alert(res.message); // 
          // 
          // if ( res.message === '') { //
          this.administrators = this.administrators.filter(h => h !== administrator); // 
          this.dataShow = [];
          let startNum = (this.currentPag - 1) * this.pagNums;
          for (let i = 0; i < this.pagNums; i++, startNum++) {
            if (this.administrators[startNum] !== null && this.administrators[startNum] !== undefined) {
              this.dataShow.push(this.administrators[startNum]);
            }
          }
          this.leng = this.administrators.length;
        });
    } else {
      return;
    }
  }
}
